.mdb-footer {
    text-align: center;
    font-size: 12px;
    color: #A1D1E6;
    border-top: 1px dashed rgba(255,255,255,0.1);
    padding: 15px 0;
    margin-top: 30px;
    letter-spacing: 2px;
}

.mdb-footer a {
    color: #01d277;
    text-decoration: underline;
    transition: 0.2s all;
}

.mdb-footer a:hover {
    color: #D1225B;
}

.mdb-footer p {
    margin-bottom: 5px;
}