/***
Style Guide Colours

#081b23 - dark blue
#01d277 - teal
#D1225B - pink
#4902A3 - purple
#e3f4fc - off white

***/

body {
  background: #081b23;
  color: #e3f4fc;
  font-family: 'Roboto', sans-serif;
}

.mdb-app {
  font-family: 'Montserrat', sans-serif;
  max-width: 1140px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #E3F4FC;
  font-family: 'Montserrat', sans-serif;
}

h1.bold,
h2.bold,
h3.bold {
  font-weight: 700;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 28px;
}

h3 { 
  font-size: 20px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 12px;
}

p {
  display: block;
  width: 100%;
  font-family: 'Roboto', sans-serif;
}

input {
  padding: 15px;
  border-radius: 22px;
  border-width: 0px;
  border: 2px solid rgba(255,255,255,0);
  transition: 0.5s all;
  height: 44px;
  font-size: 14px;
  font-weight: 500;
  color: #01d277;
  font-family: 'Roboto', sans-serif;
}

input:hover,
input:focus {
  border: 2px solid #01d277;
  outline: 0;
}

input::placeholder {
  color: #01d277;
}

.mdb-app__search-input-container {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5);
}

.mdb-app__search-input-container,
input.mdb-app__search-input {
  width: 342px;
  position: relative;
  margin: 0 auto;
}

.mdb-app__search-input-icon {
  position: absolute;
  z-index: 1;
  top: 15px;
  right: 15px;
  transition: 0.3s all;
  opacity: 1;
}

.mdb-app__search-input-container:hover .mdb-app__search-input-icon {
  right: 5px;
  opacity: 0;
}

@media (max-width: 372px) {
  .mdb-app__search-input-container {
    width: 90%;
  }

  input.mdb-app__search-input {
    width: 100%;
    padding: 0 15px;
  }
}

.mdb-app__catalogue-container h3 {
  margin-bottom: 20px;
}

@media (min-width: 1199px) {
  .mdb-app__catalogue-container h3 {
    margin-left: 30px;
  }  
}

@media (max-width: 576px) {
  .col-xs-6 {
    width: 50%!important;
  }
}

@media (max-width: 325px) {
  .col-xs-6 {
    width: 100%!important;
  }
}

button.mdb-back-button {
  display: flex;
  justify-content: center;
  position: absolute;
  border-width: 0px;
  top: 25px;
  left: 25px;
  font-size: 0px;
  background-color: rgba(255,255,255,0);
  border-radius: 50%;
  height: 35px;
  width: 35px;
  transition: 0.2s all;
}

button.mdb-back-button:hover {
  background-color: #01d277;
}

button.mdb-back-button:focus {
  outline: 0;
}

/*** Animations ***/
.fade-in {
  -webkit-animation: fadein 0.5s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.5s; /* Firefox < 16 */
  -ms-animation: fadein 0.5s; /* Internet Explorer */
  -o-animation: fadein 0.5s; /* Opera < 12.1 */
  animation: fadein 0.5s;
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/*** End Animations ***/